    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}
@media (min-width: 1150px) {
  .main-nav {
    display: grid;
    grid-template-areas: "navigation logo features" "promo promo promo";
    grid-template-columns: 2fr 1fr 2fr;
