}
.main-nav__background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  border-bottom: 1px solid #ECECEC;
}
@media (max-width: 767px) {
