  }
}
.main-nav__list {
  z-index: 1;
}
@media (max-width: 767px) {
  .main-nav__list {
    transition: transform 100ms ease-out;
  }
}
.main-nav__wrapper-logo {
  position: relative;
  grid-area: logo;
}
.navigation-list {
  grid-area: navigation;
}
.features-list {
  grid-area: features;
}
.promo-block {
