}
@media (max-width: 767px) {
  .toggle-menu-off {
    transform: translate(-50px, 0) scale(0.5);
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}
.main-nav__wrapper-logo {
  display: flex;
  align-items: center;
  height: 80px;
  border-bottom: 1px solid #ECECEC;
}
@media (min-width: 768px) {
