}
.main-nav__toggle {
  position: absolute;
  right: 30px;
  width: 20px;
  height: 2px;
  background-color: black;
  border: none;
}
.main-nav__toggle::before,
.main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 2px;
  background-color: black;
  transition: all 200ms;
}
.main-nav__toggle::before {
  transform: translate(0, 6px);
}
.main-nav__toggle::after {
  transform: translate(0, -6px);
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle--closed {
  background-color: transparent;
}
.main-nav__toggle--closed::before {
  top: 0;
  width: 25px;
  transform: rotate(45deg) rotate(180deg);
}
.main-nav__toggle--closed::after {
  top: 0;
  width: 25px;
  transform: rotate(-45deg) rotate(180deg);
}
.toggle-no-js {
  display: none;
}
